import React, { useState} from 'react';
// import {Logo} from '../common/Icons';
// import {isBrowser} from '../common/utils';
import { useLocation } from "@reach/router";
import Link from '../common/Link';


class Header extends React.Component{

componentDidMount(){
    console.log(this.props);
    
}
handleChange=(e)=>{
    if(e.target.checked){
        document.body.style.overflow='hidden';
    }else{
        document.body.style.overflow='auto';
    }
}

   
render(){
    return (
        <header>
            <div className="container">
                <div className="row">
                    <div className="col-xl-4 col-lg-3 col-md-7">
                            <Link href="/"  className='logo onePrep-sprite'></Link>
                        {/* <a href="./" className=""></a> */}
                    </div>
                    <div className="col-xl-8 col-lg-9 col-md-5">
                        <div className="toggle">
                            <input onChange={(e)=>this.handleChange(e)} type="checkbox" id="toggle-menu" />
                            <label className="toggle-btn" htmlFor="toggle-menu"></label>
                            <Toggle />
                        </div>
                    </div>
                </div>
            </div>
        </header>
    );
}
    
};

export default Header;


// console.log(path);


const Toggle = () =>{ 
    // console.log(useLocation());
    
    const pathName=useLocation().pathname;
   return (
    <ul className="list-inline menu-list text-right text-md-left f-16" htmlFor="toggle">
        <li className={ pathName === '/' ? "active theme-color" : ''}><Link href="/" >Home</Link></li>
        <li className={pathName === "/aboutus" ? "active text-color": ''}><Link href="/aboutus">Faculty</Link></li>
        <li className={pathName === "/features" ? "active text-color" : ''}><Link href="/features">Features</Link></li>
        <li className={pathName === "/contact" ? "active text-color" : ''}><Link href="/contact">Contact</Link></li>
        {/* <li><a href="" className="btn">Sign In</a></li> */}
    </ul>
)}


