import React,{useState,useEffect} from 'react';
import Validator from 'validator';
import PropTypes from 'prop-types';

/*

    validFor=> email,mobileNumber,text,number

    and also get 
    isValid from event.isValid =>bool
    
*/


const Input = ({ type, validFor, className, validClassName, invalidClassName, onChange, id, name, value, style, placeholder }) => {

    const [valueUse, setValue] = useState(value ? value : "");
    const [valid, setValid] = useState(null);

   
    useEffect(()=>{
        setValue(value)
    },[value])

    const onChangeInput = (e) => {
        const event = { ...e };
        let isValidLocal = 0;
        // console.log(value)

        switch (validFor) {
            case "mobileNumber":
                (!isNaN(e.target.value) && String(e.target.value).length >= 10) ? (isValidLocal = 1) : (isValidLocal = 0);
                if (!isNaN(e.target.value)) {
                    setValue(e.target.value);
                    event.isValid = isValidLocal;
                    setValid(isValidLocal);
                    event.target.value = e.target.value;
                    return onChange(event);
                }
                return;
            case "number":
                !isNaN(e.target.value) ? (isValidLocal = 1) : (isValidLocal = 0);
                break;
            case "email":
                Validator.isEmail(e.target.value) ? (isValidLocal = 1) : (isValidLocal = 0);
                break;
            case "text":
                e.target.value.trim() ? (isValidLocal = 1) : (isValidLocal = 0);
                break;

            case "textarea":
                e.target.value.trim() ? (isValidLocal = 1) : (isValidLocal = 0);
                break;

            default:
                setValid(null);
                break;
        }
        setValid(isValidLocal)
        setValue(e.target.value);
        event.isValid = isValidLocal;
        onChange(event)
    }
    // console.log(valid,valueUse);
    return (
        validFor==="textarea"
        ?
        <textarea 
            type={validFor === "mobileNumber" ? "tel" : validFor ? validFor : type}
            style={style ? style : {}}
            value={valueUse}
            placeholder={placeholder ? placeholder : ""}
            className={`${className ? className : ""} ${valid === null ? "" : valid ? validClassName ? validClassName : "valid" : invalidClassName ? invalidClassName : "invalid"}`}
            id={id ? id : ""}
            name={name ? name : ""}
            onChange={onChangeInput}
        />
        :
        <input
            type={validFor === "mobileNumber" ? "tel" : validFor ? validFor : type}
            style={style ? style : {}}
            value={valueUse}
            placeholder={placeholder ? placeholder : ""}
            className={`${className ? className : ""} ${valid === null ? "" : valid ? validClassName ? validClassName : "valid" : invalidClassName ? invalidClassName : "invalid"}`}
            id={id ? id : ""}
            name={name ? name : ""}
            onChange={onChangeInput}
        />
    );
};

Input.propTypes = {
    type:PropTypes.string.isRequired,
    validFor:PropTypes.oneOf(['email', 'mobileNumber',"text","number", "textarea"]),
    className:PropTypes.string,
    validClassName:PropTypes.string,
    invalidClassName:PropTypes.string,
    onChange:PropTypes.func,
    id:PropTypes.string,
    name:PropTypes.string,
    value:PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.number
    ]),
    style:PropTypes.object,
    placeholder:PropTypes.string
   };

export default Input;